import React, { FC, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  enumFormatter,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CalculoReferencia, CLASS_NAME } from '../../../../models/dtos/mfp/calculoReferencia';
import { TIPO_CALCULO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoCalculoEnum';
import { TIPO_PAGAMENTO_MFP_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoPagamentoMFPEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CalculoReferenciaSearchView: FC<SearchViewProps<CalculoReferencia>> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [expression, setExpression] = useState<SearchExpression[]>(undefined);

  function getExpressions() {
    return [
      {
        fieldName: 'empresaMFP.id',
        value: globalParameter.empresaMFP.id,
      },
    ];
  }

  useEffect(
    () => {
      setExpression(getExpressions());
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'diaInicial',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia Inicial',
    },
    {
      dataField: 'diaFinal',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Dia Final',
    },
    {
      dataField: 'tipoCalculo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatExtraData: TIPO_CALCULO_ENUM_VALUES,
      formatter: enumFormatter,
      sort: true,
      text: 'Tipo de Cálculo',
    },
    {
      dataField: 'tipoPagamento',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      formatExtraData: TIPO_PAGAMENTO_MFP_ENUM_VALUES,
      formatter: enumFormatter,
      sort: true,
      text: 'Tipo de Pagamento',
    },
    {
      dataField: 'dtPagamento',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Dt. Pagamento',
    },
  ];

  return (
    <WindowComponent title="Histórico de Cálculos">
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        defaultSearchExpressions={undefined}
        searchExpressionsProps={expression}
        extraData={{
          ...getExtraData(props, '', CLASS_NAME),
          hideActionColumn: true,
        }} // TODO implementar restante das regras de negócio
      />
    </WindowComponent>
  );
};

export default CalculoReferenciaSearchView;
